<template>
  <div>
    <a-modal 
      v-model="visible" 
      :confirmLoading="loading" 
      :maskClosable="false" 
      @cancel="cancel" 
      @ok="confirm" 
      :width="800"
    >
      <div slot="title">采购订单</div>

      <div class="modal-body">
        <h3>基本信息</h3>

        <div class="row">
          <div class="columns">
            <span class="label">采购单ID:</span>
            <span class="value">{{ form.id }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">采购单号:</span>
            <a-input v-if="form.purchase_id" v-model="form.purchase_id" style="width: 200px;" />
            <span v-else class="value">{{ form.purchase_id }}</span>
          </div>
        </div>
        <div class="row">
          <div class="columns">
            <span class="label">运营咨询中:</span>
            <a-radio-group v-model="form.is_error">
              <a-radio :value="true">是</a-radio>
              <a-radio :value="false">否</a-radio>
            </a-radio-group>
          </div>
        </div>
        
        <div class="row">
          <div class="columns">
            <span class="label" style="width: 71px;">国内运单号:</span>
            <div>
              <div v-for="posting_number,index in posting_numbers" :key="link+index">
                <span>运单号{{ index+1 }}号：</span>
                <a-input v-model="posting_numbers[index].value" style="width: 200px; margin-right: 10px;"/>
                <a-select v-model="posting_numbers[index].signed" placeholder="请选择" style="width: 100px; margin-right: 10px;"> 
                  <a-select-option v-for="option in signed" :value="option" :key="option">{{ option }}</a-select-option> 
                </a-select>
                <a-button type="danger" icon="delete" size="small" @click="delPostingNumbers(index)">删除</a-button>
              </div>
            </div>
          </div>
          <a-button icon="edit" size="small" @click="addPostingNumbers">新增国内运单号</a-button>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">待采购量:</span>
            <span class="value">{{ form.wait_for_purchase_quantity }}</span>
          </div>
          <div class="columns">
            <span class="label">在篮量:</span>
            <span class="value">{{ form.in_basket_quantity }}</span>
          </div>
          <div class="columns">
            <span class="label">在途量:</span>
            <span class="value">{{ form.in_transit_quantity }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">库存量:</span>
            <span class="value">{{ form.stock_quantity }}</span>
          </div>
          <div class="columns">
            <span class="label">出库量:</span>
            <span class="value">{{ form.out_stock_quantity }}</span>
          </div>
          <div class="columns">
            <span class="label">丢失量:</span>
            <span class="value">{{ form.loss_quantity }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">采购价格:</span>
            <a-input v-model="form.price" style="width: 100px;"/>
          </div>
          <div class="columns">
            <span class="label">运费:</span>
            <a-input v-model="form.shipping_fee" style="width: 100px;"/>
          </div>
          <div class="columns">
            <span class="label">退货费用:</span>
            <a-input v-model="form.back_fee" style="width: 100px;"/>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">物流状态:</span>
            <a-input v-model="form.logistics_status" style="width: 535px;"/>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">采购平台:</span>
            <a-select v-model="form.purchase_platform" placeholder="请选择" style="width: 200px;"> 
              <a-select-option v-for="option in options" :value="option" :key="option">{{ option }}</a-select-option> 
            </a-select> 
          </div>

          <div class="columns">
            <span class="label" style="width: 115px;">采购状态:</span>
            <a-select v-model="form.status" placeholder="请选择" style="width: 200px;"> 
              <a-select-option v-for="option in options_status" :value="option" :key="option">{{ option }}</a-select-option> 
            </a-select> 
          </div>
        </div>
        
        <div>
          <h3 style="margin-top: 10px;">关系统内商品信息</h3>

          <div class="row">
            <div class="columns">
              <img :src="form.system_product.primary_image" style="width: 50px; height: 50px;" alt="产品主图" />
            </div>
          </div>

          <div class="row">
            <div class="columns">
              <span class="label">商品ID:</span>
              <span class="value">{{ form.system_product.id }}</span>
            </div>
          </div>
          
          <div class="row">
            <div class="columns">
              <span class="label">系统sku:</span>
              <span class="value">{{ form.system_product.system_sku }}</span>
            </div>
          </div>

          <div class="row">
            <div class="columns">
              <span class="label">参考重量:</span>
              <span class="value">{{ form.system_product.reference_weight }}</span>
            </div>
          </div>

          <div class="row">
            <div class="columns">
              <span class="label">参考价格:</span>
              <span class="value">{{ form.system_product.reference_cost }}</span>
            </div>
          </div>

          <div class="row">
            <div class="columns">
              <span class="label">采购链接:</span>
              <div>
                <div v-for=" link,index in JSON.parse(form.system_product.purchase_link)" :key="index+100">
                  <span>{{ index+1 }}：</span>
                  <a :href="link.link" target="_blank"> {{ `链接${index+1}号` }} </a>
                  <span> {{ link.msg }} </span>
                </div>
              </div>
            </div>
          </div>

          <div class="row">
            <div class="columns">
              <span class="label">采购平台:</span>
              <span class="value">{{ form.system_product.purchase_platform }}</span>
            </div>
          </div>

          <div class="row">
            <div class="columns">
              <span class="label">供应商名称:</span>
              <span class="value">{{ form.system_product.supplier_name }}</span>
            </div>
          </div>

          <div class="row">
            <div class="columns">
              <span class="label">待采购量:</span>
              <span class="value">{{ form.system_product.wait_for_purchase_quantity }}</span>
            </div>
            <div class="columns">
              <span class="label">在篮量:</span>
              <span class="value">{{ form.system_product.in_basket_quantity }}</span>
            </div>
            <div class="columns">
              <span class="label">在途量:</span>
              <span class="value">{{ form.system_product.in_transit_quantity }}</span>
            </div>
          </div>

          <div class="row">
            <div class="columns">
              <span class="label">库存量:</span>
              <span class="value">{{ form.system_product.stock_quantity }}</span>
            </div>
            <div class="columns">
              <span class="label">出库量:</span>
              <span class="value">{{ form.system_product.out_stock_quantity }}</span>
            </div>
            <div class="columns">
              <span class="label">丢失量:</span>
              <span class="value">{{ form.system_product.loss_quantity }}</span>
            </div>
          </div>
        </div>

        <div>
          <h3 style="margin-top: 10px;">采购商品信息</h3>
          <div v-for="(purchase_product, index) in form.purchase_products" :key="purchase_product.id" style="display: flex; align-items: center;">
            <span style="margin-right: 8px;">{{ index+1 }}.</span>
            <div style="margin-top:5px; border:1px solid black;">
              <div class="row">
                <div class="columns">
                  <span class="label">采购商品ID:</span>
                  <span class="value">{{ purchase_product.id }}</span>
                </div>
              </div>

              <div class="row">
                <div class="columns">
                  <span class="label">采购商品sku:</span>
                  <span class="value">{{ purchase_product.sku }}</span>
                </div>
              </div>

              <div class="row">
                <div class="columns">
                  <span class="label">采购价格:</span>
                  <span class="value">{{ purchase_product.price }}</span>
                </div>
                
                <div class="columns">
                  <span class="label">采购类型:</span>
                  <span class="value">{{ purchase_product.type }}</span>
                </div>

                <div class="columns">
                  <span class="label">采购状态:</span>
                  <span class="value">{{ purchase_product.status }}</span>
                </div>
              </div>

              <div class="row">
                <div class="columns">
                  <span class="label">创建时间:</span>
                  <span class="value">{{ purchase_product.create_time }}</span>
                </div>

                <div class="columns">
                  <span class="label">入库时间:</span>
                  <span class="value">{{ purchase_product.stock_in_date }}</span>
                </div>
              </div>

              <div class="row">
                <div class="columns">
                  <span class="label">采购单ID:</span>
                  <span class="value">{{ purchase_product.purchase_order_id }}</span>
                </div>
              </div>

              <div class="row">
                <div class="columns">
                  <span class="label">系统内商品ID:</span>
                  <span class="value">{{ purchase_product.system_product_id }}</span>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div>
          <h3>留言</h3>
          <div class="msg_area" style="width: 98%; padding:5px; border: 1px solid black; height: 400px; overflow: auto; margin-bottom: 20px">
            <div v-for="word_msg, index in JSON.parse(form.error_words)" :key="index + 100" style="display:flex; flex-direction: column; margin-bottom: 10px">
              
              <div style="margin-right:10px">{{  `${word_msg.date}  ${word_msg.username} (${word_msg.role})` }}:</div>
              
              <div v-if="word_msg.type == 'word'">
                <div>{{ word_msg.content }}</div>
              </div>
              <div v-else-if="word_msg.type == 'picture'">
                <img :src="url + word_msg.content" style="width: 100px; cursor: pointer;" alt="图片" @click="enlargeImage(url + word_msg.content)" />
              </div>
            </div>
          </div>
          <div class="input_area">
            <a-input v-model="msg" aria-placeholder="请输入异常信息..." style="width: 535px;" />
            <a-button icon="edit" size="small" style="margin:0 10px" @click="add_msg">发送</a-button>
            <a-button icon="edit" size="small" @click="triggerFileInput">图片上传</a-button>
            <input type="file" ref="fileInput" @change="add_pic" style="display: none;" />
          </div>
        </div>
      </div>
    </a-modal>

    <!-- 图片放大显示的模态框 -->
    <a-modal
      v-model="imageModalVisible"
      :width="800"
      :style="{ textAlign: 'center' }"
      @cancel="imageModalVisible = false"
    >
      <img :src="currentImage" style="max-width: 100%; max-height: 600px;" alt="放大图片" />
    </a-modal>
  </div>
</template>


<script>
import { modifyData,uploadImage } from '@/api/purchaser'
import { message } from "ant-design-vue";
import config from "@/utils/config"

export default {
  name: 'FormModal',
  props: ['visible', 'form'],
  model: { prop: 'visible', event: 'cancel' },
  data() {
    return {
      loading: false,
      options:["pdd","1688","线下"],
      options_status:["待采购","运输中","已完成","有异常","已作废"],
      posting_numbers:this.form.posting_numbers?JSON.parse(this.form.posting_numbers):[],
      error_words:this.form.error_words?JSON.parse(this.form.error_words):[],
      msg : "",
      signed:["未入库","已入库"],
      url: config.baseURL,
      imageModalVisible: false, // 控制图片模态框的显示
      currentImage: "", // 当前点击的图片地址
    };
  },
  computed: {
    username() {
      return this.$store.state.user.username
    },
  },
  watch:{
    posting_numbers:{
      handler(newValue){
        this.form.posting_numbers = JSON.stringify(newValue)
      },
      deep:true
    },
    error_words:{
      handler(newValue){
        this.form.error_words = JSON.stringify(newValue)
      },
      deep:true
    },
    visible(newValue) {
      this.posting_numbers = this.form.posting_numbers?JSON.parse(this.form.posting_numbers):[]
      this.error_words = this.form.error_words?JSON.parse(this.form.error_words):[]
    }
  },
  methods: {
    confirm() {
      console.log(this.form)
      modifyData(this.form).then(
        res => {
          message.success(res.msg)
          this.$emit('update', false);
        }
      )

    },
    cancel() {
      this.$emit('cancel', false);
    },
    addPostingNumbers(){
      this.posting_numbers.push({value:"",signed:"未入库"})
    },
    delPostingNumbers(index){
      this.posting_numbers.splice(index,1)
    },
    add_msg(){
      this.error_words.push({username:this.username,type:'word',content:this.msg,date:this.formatCurrentTime(), role:'采购'})
      this.msg = ""
    },
    triggerFileInput() {
      this.$refs.fileInput.click();
    },
    add_pic(event) {
      const file = event.target.files[0]; // 获取选中的文件
      if (!file) return;

      const formData = new FormData();
      formData.append('file', file);

      uploadImage(formData).then((res) => {
        if (res.code === 200) {
          this.error_words.push({ username:this.username, type:'picture', content:res.data.url, date:this.formatCurrentTime(), role:'采购'});
          message.success(res.data.msg);
        } else {
          message.error('图片上传失败，请重试！');
        }
      });
    },
    enlargeImage(imageUrl) {
      this.currentImage = imageUrl; // 设置当前图片
      this.imageModalVisible = true; // 打开模态框
    },
    formatCurrentTime() {
        const now = new Date(); // 获取当前时间

        // 获取年、月、日、时、分、秒
        const year = now.getFullYear(); // 年份
        const month = now.getMonth() + 1; // 月份，getMonth()返回的月份从0开始，所以需要+1
        const day = now.getDate(); // 日
        const hours = now.getHours(); // 小时
        const minutes = now.getMinutes(); // 分钟
        const seconds = now.getSeconds(); // 秒数

        // 将单个数字格式化为两位数（如1变为01）
        const formatTwoDigits = (number) => number < 10 ? '0' + number : number;

        // 组合格式化后的日期和时间
        const formattedTime = `${year}年${formatTwoDigits(month)}月${formatTwoDigits(day)}日 ${formatTwoDigits(hours)}:${formatTwoDigits(minutes)}:${formatTwoDigits(seconds)}`;

        return formattedTime;
    }
  },
  mounted(){
    
  }
}
</script>

<style scoped>
.modal-body { 
  max-height: 60vh;
  overflow-y: auto; 
}

.row {
  display: flex;
  align-items: center;
  margin-top: 5px;
}

.row .columns {
  display: flex;
  margin-right: 30px;
  align-items: center
}

.row .columns .label{
  margin-right: 10px;
}

.row .columns .value{
  margin-right: 5px;
  color: green;
}
</style>